.block-features .block__items {
  display: grid;
  flex-flow: row wrap;
  grid-auto-flow: row;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 30px;
}

.item-features {
  background: #fff;
  outline: 1px solid #DBDBDB;
  border: 0;
}

.item-features:hover {
  box-shadow: 0 5px 10px 2px rgba(34, 60, 80, 0.2);
}

.item-features h3 {
  margin: 0;
  font-size: 20px;
  font-weight: 500;
  line-height: 28px;
  color: #616161;
}

.item-features .item-features__link {
  display: flex;
  flex-flow: row wrap;
  grid-template-rows: 1fr 1fr auto;
  border: 0;
  height: 100%;
  align-content: space-between;
}

.item-features .item-features__link .btn_link {
  min-height: 15px;
}

.item-features .item-features__link .btn_link:before {
  margin-left: 0;
}

.item-features .item-features__link:hover .btn_link:before {
  margin-left: 15px;
  margin-right: 0;
}

.item-features .item-features__link:hover .btn_link span {
  display: inline-block;
  color: #616161;
  font-weight: 500;
}

.item-features:last-child {
  margin-right: 0;
}

.item-features__image {
  margin: 40px 40px 0;
  flex: 1 100%;
}

.item-features__image img {
  height: 71px;
}

.item-features__title {
  margin: 25px 40px;
  flex: 1 100%;
  color: #999999;
}

.item-features__ft {
  border-top: 1px solid #cdcdcd;
  padding: 27px 33px;
  line-height: 0px;
  flex: 1 100%;
}

.item-features__ft span,
.item-features__ft svg {
  display: none;
}

@media only screen and (max-width: 1024px) {
  .item-features__title {
    margin: 20px 33px;
  }
}

@media only screen and (max-width: 1023px) {
  .block-features .block__items {
    min-height: initial;
    grid-gap: 20px;
  }

  .item-features h3 {
    font-size: 15px;
    line-height: 20px;
  }

  .item-features__image {
    margin: 35px 35px 0;
  }

  .item-features__image img {
    min-height: 54px;
    min-width: 54px;
    height: 54px;
    width: 54px;
  }

  .item-features__ft {
    padding: 18px 33px 19px;
  }
}

@media only screen and (max-width: 767px) {
  .block-features .block__items {
    grid-template-columns: 1fr;
    grid-gap: 20px;
  }

  .block-features .block__items {
    min-height: initial;
    grid-gap: 30px;
  }

  .item-features .item-features__link {
    justify-content: unset;
    padding: 18px 19px;
    align-items: center;
    flex-flow: row nowrap;
  }

  .item-features .item-features__link .btn_link:before {
    margin-left: 0;
    margin-right: 0;
    display: none;
  }

  .item-features .item-features__link .btn_link span {
    display: none !important;
    flex: 0 50px;
  }

  .item-features .item-features__link svg {
    display: block;
  }

  .item-features__image {
    margin: 0 10px 0 10px;
    flex: 50px;
  }

  .item-features__image img {
    min-height: 31px;
    min-width: 31px;
    height: 31px;
    width: 31px;
  }

  .item-features__title {
    padding-right: 10px;
    margin-right: 10px;
    margin: 0 10px 0 0;
  }

  .item-features__ft {
    border-top: 0px solid #cdcdcd;
    padding: 0;
    flex: 1;
    display: flex;
  }
}